import React from "react";
import MainSlider from "./MainSlider";
import WorkroomImage from "../image/workroom8363.png";
import MaydayImage from "../image/smilemaydayicon.png";
import OtherprojectImage from "../image/otherproject.png";
import PromiseElection from "../image/koreaColormap.gif";
// import WhiteImage from "../image/WhiteImage";
const MainSliderList = () => {
  const values = [
    {
      mainLink: "/project",
      title: "Project",
      topic1: "MAYDAY",
      _src1: MaydayImage,
      _link1: "/project/mayday",
      alt1: "mayday",
      topic2: "Workroom8363",
      _src2: WorkroomImage,
      _link2: "/project/workroom8363",
      alt2: "workroom8363",
      topic3: "AlgoPPobja",
      _src3: PromiseElection,
      _link3: "/project/algoppobja",
      alt3: "other",
      _src4: OtherprojectImage,
      _link4: "/project",
      id: "#circle"
    },
    {
      mainLink: "/development",
      title: "Development",
      topic1: "JavaScript",
      _src1:
        "data:image/png;base64,",
      _link1: "/development/javascript",
      alt1: "JavaScript",
      topic2: "TypeScript",
      _src2:
        "https://pbs.twimg.com/profile_images/1149708719178993664/3Hb8W4aX_400x400.png",
      _link2: "/development/typescript",
      alt2: "TypeScript",
      topic3: "React",
      _src3: "https://hackernoon.com/hn-images/1*To2H39eauxaeYxYMtV1afQ.png",
      _link3: "/development/react",
      alt3: "React",
      topic4: "SASS",
      _src4:
        "data:image/png;base64,",
      _link4: "/development/sass",
      alt4: "SASS",
      _src5: OtherprojectImage,
      _link5: "/development"
    },
    {
      mainLink: "/travel",
      title: "Travel",
      topic1: "Saipan",
      _src1: "https://static.thenounproject.com/png/1042874-200.png",
      _link1: "/travel/saipan",
      topic2: "Kota Kinabalu",
      _src2: "https://static.thenounproject.com/png/585798-200.png",
      _link2: "/travel/koki",
      topic3: "Osaka",
      _src3: "https://static.thenounproject.com/png/4499-200.png",
      _link3: "/travel/osaka",

      _link4: "/travel",
      _src4: OtherprojectImage
    }
  ];

  return (
    <div className="MainSlider">
      {values.map((value, index) => {
        return (
          <MainSlider
            mainLink={value.mainLink}
            title={value.title}
            topic1={value.topic1}
            _src1={value._src1}
            _link1={value._link1}
            topic2={value.topic2}
            _src2={value._src2}
            _link2={value._link2}
            topic3={value.topic3}
            _src3={value._src3}
            _link3={value._link3}
            topic4={value.topic4}
            _src4={value._src4}
            _link4={value._link4}
            topic5={value.topic5}
            _src5={value._src5}
            _link5={value._link5}
            key={index}
          />
        );
      })}
    </div>
  );
};

export default MainSliderList;
