import React from "react";
import Title from "../../Title";
import BasicPageComp from "../../BasicPageComp";

const SassList = () => {
  const sassValue = [
    {
      hyperLink: "/development/sass/3",
      projectName: "Sass (3) ",
      explanation: "컴파일 방법",
      addEx: "Kilikili",
      days: "2020.02.20",
      src:
        "data:image/png;base64,"
    },
    {
      hyperLink: "/development/sass/2",
      projectName: "Sass (2) ",
      explanation: "Sass와 Scss의 기본 문법 차이",
      addEx: "Kilikili",
      days: "2020.02.20",
      src:
        "data:image/png;base64,"
    },
    {
      hyperLink: "/development/sass/1",
      projectName: "Sass (1) ",
      explanation: "Sass - 시작",
      addEx: "Kilikili",
      days: "2020.02.20",
      src:
        "data:image/png;base64,"
    }
  ];
  return (
    <div className="projectContainer">
      <Title title="< SASS />" />
      <div className="__listContainer">
        {sassValue.map((value, index) => {
          return (
            <BasicPageComp
              hyperLink={value.hyperLink}
              projectName={value.projectName}
              explanation={value.explanation}
              addEx={value.addEx}
              days={value.days}
              src={value.src}
              alt={index}
            />
          );
        })}
      </div>
    </div>
  );
};

export default SassList;
