import React from "react";
import BasicPageComp from "../BasicPageComp";

const DevelopmentPage = () => {
  const developmentValue = [
    {
      hyperLink: "/development/javascript",
      projectName: "JavaScript",
      explanation: "JavaScript is...",
      addEx: "Kilikili",
      days: "2019.10.31",
      src:
        "data:image/png;base64,",
      alt: "JavaScript"
    },
    {
      hyperLink: "/development/typescript",
      projectName: "TypeScript",
      explanation: "TypeScript is...",
      addEx: "Kilikili",
      days: "2019.10.31",
      src:
        "https://pbs.twimg.com/profile_images/1149708719178993664/3Hb8W4aX_400x400.png",
      alt: "TypeScript"
    },
    {
      hyperLink: "/development/react",
      projectName: "React",
      explanation: "React is ...",
      addEx: "Kilikili",
      days: "2019.12.15",
      src: "https://hackernoon.com/hn-images/1*To2H39eauxaeYxYMtV1afQ.png",
      alt: " React"
    },
    {
      hyperLink: "/development/sass",
      projectName: "Sass",
      explanation: "Sass is ...",
      addEx: "Kilikili",
      days: "2020.02.19",
      src:
        "data:image/png;base64,",
      alt: "sass"
    }
  ];

  return (
    <div className="__listContainer">
      {developmentValue.map((value, index) => {
        return (
          <BasicPageComp
            hyperLink={value.hyperLink}
            projectName={value.projectName}
            explanation={value.explanation}
            addEx={value.addEx}
            days={value.days}
            src={value.src}
            alt={value.alt}
          />
        );
      })}
    </div>
  );
};

export default DevelopmentPage;
